import { type FC } from "react";
import { Element } from 'react-scroll';

type TProps = {
  bg?: string,
  title?: string,
  h?: string,
}

export const PlaceholderWidget: FC<TProps> = ({bg,title,h}) => <div style={{
  backgroundColor:bg,
  height: h,
  display: 'flex',
  justifyContent: 'center',
}}>
  <Element name={title!} />
  {title}
</div>

PlaceholderWidget.defaultProps = {
  bg: 'crimson',
  title: 'Placeholder',
  h: '100vh',
}
