import styles from './app.module.scss';
import me from 'assets/images/me.jpeg';
import lottieMenu from 'assets/lottie/menu.json';
import {PlaceholderWidget} from 'widgets';
import { useState, useCallback } from 'react';
import { useLottie } from 'lottie-react';
import { scroller } from 'react-scroll';

export const App = () => {

  const [isMenuOpen, setMenuOpen] = useState(false);
  const {View, playSegments}  = useLottie({
    animationData: lottieMenu,
    autoplay: false,
  });

  const toSection = useCallback((section?: string) => {
    playSegments(isMenuOpen ? [70, 0] : [0,70], true);
    setMenuOpen(prev => !prev);
    if (section) {
      scroller.scrollTo(section, {
        duration: 800,
        smooth: 'easeInOUtQuart',
        delay: 0,
      });
    }
  }, [isMenuOpen,playSegments]);

  return <>
    <nav className={isMenuOpen ? styles.menuOpen : ''}>
      <div>
        <div style={{width: '36px', height: '36px'}} onClick={() => {
          playSegments(isMenuOpen ? [70, 0] : [0,70], true);
          setMenuOpen(prev => !prev);
        }}>
          {View}
        </div>
      </div>
      <div>Home</div>
      <div onClick={() => toSection('Second Section')}>Second Section</div>
      <div onClick={() => toSection('Third Section')}>Third Section</div>
    </nav>
    <div className={styles.header}>
      <div className={styles.card}>
        <div>
          <div>
            <img src={me} alt="me" />
          </div>
          <div>
            <span>LIU, CHANG</span>
            <span> LOS ANGELES</span>
          </div>
        </div>
      </div>
      <span className={styles.headerTitle}>Hello, me Chang. Nice meeting you!</span>
    </div>
    <PlaceholderWidget title='Second Section' />
    <PlaceholderWidget title='Third Section' bg='coral' />
    <div className={styles.footer}>

    </div>
  </>
}
